'use client';

import React, { useState, useCallback, useEffect } from 'react';
import { Play, Pause, RotateCcw, Settings, Download, Upload, Eye, EyeOff } from 'lucide-react';
import { cn } from '@/app/lib/utils';

interface InteractiveCodeEditorProps {
  className?: string;
  initialCode?: string;
  language?: string;
  theme?: 'light' | 'dark';
  onCodeChange?: (code: string) => void;
  onRun?: (code: string) => void;
  onSave?: (code: string) => void;
}

export function InteractiveCodeEditor({
  className,
  initialCode = '// Âú®ËøôÈáåÁºñÂÜô‰Ω†ÁöÑ‰ª£Á†Å\nconsole.log("Hello, World!");',
  language = 'javascript',
  theme = 'light',
  onCodeChange,
  onRun,
  onSave
}: InteractiveCodeEditorProps) {
  const [code, setCode] = useState(initialCode);
  const [output, setOutput] = useState<string[]>([]);
  const [isRunning, setIsRunning] = useState(false);
  const [showPreview, setShowPreview] = useState(true);
  const [autoRun, setAutoRun] = useState(false);
  const [fontSize, setFontSize] = useState(14);

  // Ê®°Êãü‰ª£Á†ÅÊâßË°å
  const runCode = useCallback(async () => {
    setIsRunning(true);
    setOutput([]);
    
    // Ê®°ÊãüÊâßË°åÂª∂Ëøü
    await new Promise(resolve => setTimeout(resolve, 1000));
    
    // ÁÆÄÂçïÁöÑ‰ª£Á†ÅÊâßË°åÊ®°Êãü
    try {
      const mockOutput: string[] = [];
      
      if (code.includes('console.log')) {
        const matches = code.match(/console\.log\(['"`](.+?)['"`]\)/g);
        if (matches) {
          matches.forEach(match => {
            const message = match.match(/['"`](.+?)['"`]/);
            if (message) {
              mockOutput.push(message[1]);
            }
          });
        }
      }
      
      if (code.includes('for') || code.includes('while')) {
        mockOutput.push('Âæ™ÁéØÊâßË°å‰∏≠...');
        mockOutput.push('Âæ™ÁéØÂÆåÊàê');
      }
      
      if (code.includes('function')) {
        mockOutput.push('ÂáΩÊï∞ÂÆö‰πâÊàêÂäü');
      }
      
      if (mockOutput.length === 0) {
        mockOutput.push('‰ª£Á†ÅÊâßË°åÂÆåÊàê');
      }
      
      setOutput(mockOutput);
      
      if (onRun) {
        onRun(code);
      }
    } catch (error) {
      setOutput([`ÈîôËØØ: ${error}`]);
    }
    
    setIsRunning(false);
  }, [code, onRun]);

  // Ëá™Âä®ËøêË°å
  useEffect(() => {
    if (autoRun) {
      const timer = setTimeout(() => {
        runCode();
      }, 1000);
      return () => clearTimeout(timer);
    }
  }, [code, autoRun, runCode]);

  const handleCodeChange = useCallback((newCode: string) => {
    setCode(newCode);
    if (onCodeChange) {
      onCodeChange(newCode);
    }
  }, [onCodeChange]);

  const handleSave = useCallback(() => {
    if (onSave) {
      onSave(code);
    } else {
      const blob = new Blob([code], { type: 'text/plain' });
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `code.${language === 'javascript' ? 'js' : language}`;
      a.click();
    }
  }, [code, language, onSave]);

  const handleFileUpload = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const content = e.target?.result as string;
        handleCodeChange(content);
      };
      reader.readAsText(file);
    }
  }, [handleCodeChange]);

  const clearOutput = useCallback(() => {
    setOutput([]);
  }, []);

  const resetCode = useCallback(() => {
    setCode(initialCode);
    setOutput([]);
  }, [initialCode]);

  const languageExamples = {
    javascript: [
      '// Âü∫Á°ÄËæìÂá∫\nconsole.log("Hello, World!");',
      '// Âæ™ÁéØ\nfor (let i = 0; i < 5; i++) {\n  console.log(i);\n}',
      '// ÂáΩÊï∞\nfunction greet(name) {\n  return "Hello, " + name;\n}\nconsole.log(greet("World"));'
    ],
    python: [
      '# Âü∫Á°ÄËæìÂá∫\nprint("Hello, World!")',
      '# Âæ™ÁéØ\nfor i in range(5):\n    print(i)',
      '# ÂáΩÊï∞\ndef greet(name):\n    return f"Hello, {name}"\n\nprint(greet("World"))'
    ],
    html: [
      '<!-- HTML Á§∫‰æã -->\n<div class="container">\n  <h1>Hello World</h1>\n  <p>This is a paragraph.</p>\n</div>',
      '<!-- Ë°®ÂçïÁ§∫‰æã -->\n<form>\n  <input type="text" placeholder="Enter your name">\n  <button type="submit">Submit</button>\n</form>'
    ]
  };

  return (
    <div className={cn("bg-white rounded-lg shadow-sm border border-gray-200", className)}>
      {/* Â§¥ÈÉ® */}
      <div className="flex items-center justify-between p-4 border-b border-gray-200">
        <div className="flex items-center gap-3">
          <div className="px-3 py-1 bg-blue-100 text-blue-800 rounded-full text-sm font-medium">
            {language.toUpperCase()}
          </div>
          <div className="text-sm text-gray-600">
            ÂÆûÊó∂‰ª£Á†ÅÁºñËæëÂô®
          </div>
        </div>
        
        <div className="flex items-center gap-2">
          <button
            onClick={() => setAutoRun(!autoRun)}
            className={cn(
              "px-3 py-1 rounded text-sm font-medium transition-colors",
              autoRun
                ? "bg-green-100 text-green-800"
                : "bg-gray-100 text-gray-600 hover:bg-gray-200"
            )}
          >
            {autoRun ? 'Ëá™Âä®ËøêË°å' : 'ÊâãÂä®ËøêË°å'}
          </button>
          
          <button
            onClick={() => setShowPreview(!showPreview)}
            className={cn(
              "p-2 rounded transition-colors",
              showPreview
                ? "bg-blue-100 text-blue-600"
                : "bg-gray-100 text-gray-600 hover:bg-gray-200"
            )}
            title={showPreview ? "ÈöêËóèÈ¢ÑËßà" : "ÊòæÁ§∫È¢ÑËßà"}
          >
            {showPreview ? <Eye className="h-4 w-4" /> : <EyeOff className="h-4 w-4" />}
          </button>
          
          <button
            onClick={resetCode}
            className="p-2 text-gray-600 hover:text-gray-800 hover:bg-gray-100 rounded transition-colors"
            title="ÈáçÁΩÆ‰ª£Á†Å"
          >
            <RotateCcw className="h-4 w-4" />
          </button>
          
          <button
            onClick={handleSave}
            className="p-2 text-gray-600 hover:text-gray-800 hover:bg-gray-100 rounded transition-colors"
            title="‰øùÂ≠ò‰ª£Á†Å"
          >
            <Download className="h-4 w-4" />
          </button>
          
          <label className="p-2 text-gray-600 hover:text-gray-800 hover:bg-gray-100 rounded transition-colors cursor-pointer"
                 title="‰∏ä‰º†Êñá‰ª∂">
            <Upload className="h-4 w-4" />
            <input
              type="file"
              accept=".js,.py,.html,.css,.json"
              onChange={handleFileUpload}
              className="hidden"
            />
          </label>
          
          <button
            onClick={runCode}
            disabled={isRunning}
            className={cn(
              "flex items-center gap-2 px-4 py-2 rounded-lg font-medium transition-colors",
              isRunning
                ? "bg-gray-400 text-white cursor-not-allowed"
                : "bg-green-600 text-white hover:bg-green-700"
            )}
          >
            {isRunning ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                ËøêË°å‰∏≠...
              </>
            ) : (
              <>
                <Play className="h-4 w-4" />
                ËøêË°å‰ª£Á†Å
              </>
            )}
          </button>
        </div>
      </div>

      <div className="flex" style={{ height: '500px' }}>
        {/* ‰ª£Á†ÅÁºñËæëÂô® */}
        <div className={cn("flex-1 flex flex-col", !showPreview && "w-full")}>
          {/* Â∑•ÂÖ∑Ê†è */}
          <div className="flex items-center justify-between p-3 border-b border-gray-200 bg-gray-50">
            <div className="flex items-center gap-2">
              <span className="text-sm text-gray-600">Â≠ó‰ΩìÂ§ßÂ∞è:</span>
              <input
                type="range"
                min="12"
                max="20"
                value={fontSize}
                onChange={(e) => setFontSize(Number(e.target.value))}
                className="w-20"
              />
              <span className="text-sm text-gray-600">{fontSize}px</span>
            </div>
            
            <div className="flex items-center gap-2">
              <button
                onClick={clearOutput}
                className="px-3 py-1 text-sm text-gray-600 hover:text-gray-800 hover:bg-gray-100 rounded transition-colors"
              >
                Ê∏ÖÁ©∫ËæìÂá∫
              </button>
            </div>
          </div>
          
          {/* ‰ª£Á†ÅÂå∫Âüü */}
          <div className="flex-1 p-4">
            <textarea
              value={code}
              onChange={(e) => handleCodeChange(e.target.value)}
              className="w-full h-full p-4 border border-gray-300 rounded-lg font-mono resize-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              style={{ fontSize: `${fontSize}px`, lineHeight: '1.5' }}
              spellCheck={false}
              placeholder="Âú®ËøôÈáåÁºñÂÜô‰Ω†ÁöÑ‰ª£Á†Å..."
            />
          </div>
          
          {/* ËæìÂá∫Âå∫Âüü */}
          <div className="border-t border-gray-200">
            <div className="flex items-center justify-between p-3 bg-gray-50 border-b border-gray-200">
              <div className="text-sm font-medium text-gray-900">ËæìÂá∫</div>
              <div className="text-xs text-gray-500">
                {output.length > 0 ? `${output.length} Ë°åËæìÂá∫` : 'Êó†ËæìÂá∫'}
              </div>
            </div>
            
            <div className="p-4 h-32 overflow-y-auto bg-gray-900 text-green-400 font-mono text-sm">
              {output.length === 0 ? (
                <div className="text-gray-500">ÁÇπÂáª"ËøêË°å‰ª£Á†Å"Êü•ÁúãËæìÂá∫ÁªìÊûú</div>
              ) : (
                output.map((line, index) => (
                  <div key={index} className="mb-1">
                    {line}
                  </div>
                ))
              )}
            </div>
          </div>
        </div>

        {/* È¢ÑËßàÈù¢Êùø */}
        {showPreview && (
          <div className="w-80 border-l border-gray-200 flex flex-col">
            <div className="p-4 border-b border-gray-200">
              <h3 className="font-medium text-gray-900">‰ª£Á†ÅÁ§∫‰æã</h3>
              <p className="text-sm text-gray-600 mt-1">ÈÄâÊã©Á§∫‰æãÂø´ÈÄüÂºÄÂßã</p>
            </div>
            
            <div className="flex-1 overflow-y-auto p-4 space-y-3">
              {languageExamples[language as keyof typeof languageExamples]?.map((example, index) => (
                <button
                  key={index}
                  onClick={() => handleCodeChange(example)}
                  className="w-full text-left p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors border border-gray-200"
                >
                  <div className="text-xs text-gray-600 mb-2 font-mono whitespace-pre-line">
                    {example}
                  </div>
                  <div className="text-xs text-blue-600 font-medium">ÁÇπÂáª‰ΩøÁî®Ê≠§Á§∫‰æã</div>
                </button>
              ))}
            </div>
            
            <div className="p-4 border-t border-gray-200 bg-gray-50">
              <div className="text-sm text-gray-600 mb-2">Âø´Êç∑ÈîÆ:</div>
              <div className="text-xs text-gray-500 space-y-1">
                <div>‚Ä¢ Ctrl+Enter: ËøêË°å‰ª£Á†Å</div>
                <div>‚Ä¢ Ctrl+S: ‰øùÂ≠ò‰ª£Á†Å</div>
                <div>‚Ä¢ Ctrl+R: ÈáçÁΩÆ‰ª£Á†Å</div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default InteractiveCodeEditor;